@charset "UTF-8";
body {
  background-color: #1d1d1d;
  color: white; }

.navi-wrapper {
  position: fixed;
  z-index: 3;
  background: rgba(15, 15, 15, 0.945);
  width: 100%;
  padding: 0.5rem 0; }
  .navi-wrapper a {
    color: white;
    text-decoration: none;
    margin-right: 0.5rem; }
    .navi-wrapper a:hover {
      text-decoration: none;
      color: grey; }

.navi-links,
.site-title {
  display: inline-block; }

.navi-links {
  margin-left: 1rem; }

.menu-icon,
.close-menu-icon {
  float: right;
  margin: 1rem;
  display: none;
  cursor: pointer; }

.site-title {
  margin: 0 1rem;
  padding-bottom: 0.5rem; }

.search-input {
  margin: auto; }

.movie-list {
  display: flex;
  flex-wrap: wrap;
  position: relative;
  top: 4rem;
  justify-content: center; }
  .movie-list ยง a {
    width: auto;
    height: auto; }

.movie {
  position: relative;
  width: 95%;
  margin: 0.5rem;
  display: flex;
  z-index: 0; }

.poster-img {
  position: relative;
  z-index: 0;
  width: 100%; }

.poster-info {
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  background-color: rgba(27, 27, 27, 0.9999);
  opacity: 0;
  color: white; }
  .poster-info:hover {
    opacity: 0.8;
    position: absolute;
    z-index: 0; }

/* Responsive styles */
@media only screen and (max-width: 720px) {
  .form {
    display: none; } }

@media only screen and (max-width: 600px) {
  .movie-list {
    top: 3rem; }
  .menu-icon,
  .close-menu-icon {
    display: block; }
  .navi-links {
    display: flex;
    justify-content: right;
    position: absolute;
    text-align: right;
    right: -12rem;
    top: 0;
    flex-direction: column;
    background: #0f0f0f;
    height: 100vh;
    opacity: 1;
    z-index: 2;
    padding: 1rem 2rem;
    transition: right 0.7s ease-in-out; }
    .navi-links a:first-of-type {
      margin-top: 2rem; }
    .navi-links a {
      margin-bottom: 0.4rem;
      font-size: 1.3rem; }
  .close-menu-icon {
    position: absolute;
    right: 0.5rem;
    top: 0.5rem; }
  .menu-active {
    right: 0; } }

@media only screen and (max-width: 350px) {
  .title-end {
    display: none; } }

@media only screen and (max-width: 175px) {
  .site-title {
    display: none; } }
